@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.main-nav-container{height:72px}.main-nav-container .app-bar{box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(255,255,255,0.12)}.main-nav-container .nav-links{display:flex;justify-content:center;align-items:center;min-width:100%}.main-nav-container .nav-links a{text-align:center;padding:10px;font-size:0.8rem}.main-nav-container .nav-links a:hover{background:rgba(255,255,255,0.15)}@media (min-width: 600px){.main-nav-container .nav-links a{font-size:inherit;padding:20px}}

.jumbotron{display:flex;justify-content:center;flex-direction:column;background-color:#0e0e0e;background:radial-gradient(ellipse at 60% 48%, rgba(255,255,255,0.07) 0%, rgba(14,14,14,0) 60%);height:calc(100vh - 72px)}@-moz-document url-prefix(){.jumbotron{background:unset}}@media (min-aspect-ratio: 3 / 2){.jumbotron{flex-direction:row}}.jumbotron .MuiTypography-root{display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff;flex-basis:100%;text-align:center}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root{flex-grow:0;flex-shrink:0;flex-basis:40%}}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root{align-items:flex-end}}.jumbotron .MuiTypography-root .name{font-size:4rem;font-weight:200;line-height:1;padding:0 20px}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root .name{font-size:5rem}}@media (min-aspect-ratio: 3 / 2) and (max-width: 1280px){.jumbotron .MuiTypography-root .name{font-size:4rem}}@media (min-aspect-ratio: 3 / 2) and (max-width: 1024px){.jumbotron .MuiTypography-root .name{font-size:2.5rem}}.jumbotron .MuiTypography-root .title{font-size:1.5rem;line-height:1;font-weight:500}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root .title{font-size:1rem;padding-right:30px}}@media (min-aspect-ratio: 3 / 2) and (min-width: 1024px){.jumbotron .MuiTypography-root .title{font-size:1.6rem;padding-right:40px}}.jumbotron .img{display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-start}.jumbotron .img img{max-height:100%}@media (min-aspect-ratio: 9 / 10) and (max-aspect-ratio: 3 / 2){.jumbotron .img img{max-width:80%;margin:0 10%}}

*{box-sizing:border-box;padding:0;margin:0}body{background-color:#0e0e0e;width:100vw}div,header,section,article{width:100%}img{max-width:100%}div{height:100%}

